import React, { Component } from 'react';
import Slider from 'react-slick';
import { t } from '../../../../i18n';
import { withTranslation } from 'react-i18next';
import { HOME_SLIDER_NAMESPACE } from '../../../../features/Home/constants';
import icon1 from '../../../../assets/img/HomeSlider/icon1.png';
import icon2 from '../../../../assets/img/HomeSlider/icon2.png';
import icon3 from '../../../../assets/img/HomeSlider/icon3.png';
import slide1 from '../../../../assets/img/HomeSlider/slider2.png';
import slide2 from '../../../../assets/img/HomeSlider/slider4.png';
import right from '../../../../assets/img/right.png';
import left from '../../../../assets/img/left.png';

const Right = ({ className, onClick }) => (
  <button
    type="button"
    onClick={onClick}
    className={`button button--text button--icon ${className}`}
  >
    <img src={right} alt="Arrow right" />
  </button>
);
const Left = ({ className, onClick }) => (
  <button
    type="button"
    onClick={onClick}
    className={`button button--text button--icon ${className}`}
  >
    <img src={left} alt="Arrow left" />
  </button>
);
class HomeSlider extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      arrow: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      nextArrow: <Right to="next" />,
      prevArrow: <Left to="prev" />,
    };
    return (
      <Slider className="slider-home" {...settings}>
        <div className="slide-list">
          <div className="content">
            <div className="slider-texte">
              <h3>
                {`${t(`${HOME_SLIDER_NAMESPACE}with`)} `}
                <strong>
                  {`${t(`${HOME_SLIDER_NAMESPACE}atlantiqueNet`)} `}
                </strong>
                {`${t(`${HOME_SLIDER_NAMESPACE}live`)} `}
                <strong>
                  {`${t(`${HOME_SLIDER_NAMESPACE}clientExperience`)} `}
                </strong>
                {`${t(`${HOME_SLIDER_NAMESPACE}completely`)} `}
                <strong>{t(`${HOME_SLIDER_NAMESPACE}rethought`)}</strong>
              </h3>
              <p>
                {`${t(`${HOME_SLIDER_NAMESPACE}eitherYouHave`)} `}
                <strong>
                  {t(`${HOME_SLIDER_NAMESPACE}improvedExperience`)}
                </strong>
              </p>
              <ul>
                <li>
                  <img src={icon1} alt="Lamp icon" />
                  <span>
                    {t(`${HOME_SLIDER_NAMESPACE}navigation`)} <br />
                    {` ${t(`${HOME_SLIDER_NAMESPACE}intuitive`)}`}
                  </span>
                </li>
                <li>
                  <img src={icon2} alt="Fix icon" />
                  <span>
                    {t(`${HOME_SLIDER_NAMESPACE}offres`)} <br />
                    {` ${t(`${HOME_SLIDER_NAMESPACE}personalized`)}`}
                  </span>
                </li>
                <li>
                  <img src={icon3} alt="eye icon" />
                  <span>
                    {t(`${HOME_SLIDER_NAMESPACE}tracking`)} <br />
                    {` ${t(`${HOME_SLIDER_NAMESPACE}online`)}`}
                  </span>
                </li>
              </ul>
            </div>
            <div className="slider-image">
              <img src={slide1} alt="Atlantique Net" />
            </div>
          </div>
        </div>
        <div className="slide-list">
          <div className="content">
            <div className="slider-texte">
              <h3>
                {`${t(`${HOME_SLIDER_NAMESPACE}with`)} `}
                <strong>
                  {`${t(`${HOME_SLIDER_NAMESPACE}atlantiqueNet`)} `}
                </strong>
                {`${t(`${HOME_SLIDER_NAMESPACE}live`)} `}
                <strong>
                  {`${t(`${HOME_SLIDER_NAMESPACE}clientExperience`)} `}
                </strong>
                {`${t(`${HOME_SLIDER_NAMESPACE}completely`)} `}
                <strong>{t(`${HOME_SLIDER_NAMESPACE}rethought`)}</strong>
              </h3>
              <p>
                {`${t(`${HOME_SLIDER_NAMESPACE}eitherYouHave`)} `}
                <strong>
                  {t(`${HOME_SLIDER_NAMESPACE}improvedExperience`)}
                </strong>
              </p>
              <ul>
                <li>
                  <img src={icon1} alt="Lamp icon" />
                  <span>
                    {t(`${HOME_SLIDER_NAMESPACE}navigation`)} <br />
                    {` ${t(`${HOME_SLIDER_NAMESPACE}intuitive`)}`}
                  </span>
                </li>
                <li>
                  <img src={icon2} alt="Fix icon" />
                  <span>
                    {t(`${HOME_SLIDER_NAMESPACE}offres`)} <br />
                    {` ${t(`${HOME_SLIDER_NAMESPACE}personalized`)}`}
                  </span>
                </li>
                <li>
                  <img src={icon3} alt="eye icon" />
                  <span>
                    {t(`${HOME_SLIDER_NAMESPACE}tracking`)} <br />
                    {` ${t(`${HOME_SLIDER_NAMESPACE}online`)}`}
                  </span>
                </li>
              </ul>
            </div>
            <div className="slider-image">
              <img src={slide2} alt="Atlantique Net and Atlantique Mobile" />
            </div>
          </div>
        </div>
      </Slider>
    );
  }
}

export default withTranslation()(HomeSlider);
