import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';
import { t } from '../../../../i18n';
import { HOME_ACCESS_SECTION_NAMESPACE } from '../../constants';
import Background from '../../../../assets/img/bg-orange.png';
import EspaceIcon from '../../../../assets/SvgComponents/HomeSvg/EspaceIcon';
import { toggleEnrollmentModal } from '../../../../redux/enrollment/enrollment.actions';
import { showMessage } from '../../../../redux/messages/messages.actions';
import { ENROLLMENT_NAMESPACE } from '../../../Enrollment/constants';

const sectionStyle = {
  backgroundImage: `url(${Background})`,
};
class AccessSection extends React.PureComponent {
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (
      prevProps.enrollmentSuccess !== this.props.enrollmentSuccess &&
      this.props.enrollmentSuccess
    ) {
      this.props.showMessage(
        t(`${ENROLLMENT_NAMESPACE}enrollmentSuccessMessage`),
        'INFO',
        t(`${ENROLLMENT_NAMESPACE}confirm`),
      );
    }
  }
  render() {
    return (
      <div className="block-espace-client" style={sectionStyle}>
        <EspaceIcon />
        <h3> {t(`${HOME_ACCESS_SECTION_NAMESPACE}accessClientSpace`)}</h3>
        <div className="two-button">
          <Link className="btn-large btn btn-space btn-primary" to="/login">
            {` ${t(`${HOME_ACCESS_SECTION_NAMESPACE}signIn`)}`}
          </Link>
          <button
            className="btn btn-space btn-default md-trigger popin_btn"
            data-modal="abonnervous"
            onClick={this.props.toggleEnrollmentModal}
          >
            {` ${t(`${HOME_ACCESS_SECTION_NAMESPACE}signUp`)} `}
          </button>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  enrollmentSuccess: state.Enrollment.enrollmentSuccess,
});

const mapDispatchToProps = dispatch => ({
  toggleEnrollmentModal: () => {
    dispatch(toggleEnrollmentModal());
  },
  showMessage: (text, severity, validateTitle) => {
    dispatch(showMessage(text, severity, validateTitle));
  },
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(AccessSection),
);
