import React from 'react';
import { withTranslation } from 'react-i18next';
import { HOME_MOBILE_SECTION_NAMESPACE } from '../../constants';
import { t } from '../../../../i18n';
import MobilePic from '../../../../assets/img/HomeSections/Real-Photo-iPhone-Mockup-.png';

const MobileSection = () => (
  <div className="block-pocket">
    <div className="col-md-6 pocket-left">
      <img src={MobilePic} alt="Error loading" />
    </div>
    <div className="col-md-6 pocket-right">
      <h3> {t(`${HOME_MOBILE_SECTION_NAMESPACE}discoverAtlantiqueMobile`)}</h3>
      <h4> {t(`${HOME_MOBILE_SECTION_NAMESPACE}remoteBank`)}</h4>
      <ul>
        <li> {t(`${HOME_MOBILE_SECTION_NAMESPACE}accountsConsultation`)}</li>
        <li> {t(`${HOME_MOBILE_SECTION_NAMESPACE}transfers`)}</li>
        <li> {t(`${HOME_MOBILE_SECTION_NAMESPACE}cashTransfers`)}</li>
        <li> {t(`${HOME_MOBILE_SECTION_NAMESPACE}cardsManagement`)}</li>
        <li>
          {' '}
          {t(`${HOME_MOBILE_SECTION_NAMESPACE}advantegesAtlantiqueMobile`)}
        </li>
      </ul>
      <a
        className="btn-large btn btn-space btn-secondary"
        href="https://www.banqueatlantique.net/atlantique-mobile/"
        target="_blank"
      >
        {` ${t(`${HOME_MOBILE_SECTION_NAMESPACE}learnMore`)}`}
      </a>
    </div>
  </div>
);
export default withTranslation()(MobileSection);
