import React from 'react';
import { withTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { Formik } from 'formik';
import { t } from '../../../../i18n';
import {
  frequentlyAskedQuestions,
  HOME_QUESTIONS_SECTION_NAMESPACE,
} from '../../constants';
import faqIcon from '../../../../assets/img/faqIcon.png';
import FAQQuestion from '../../../../components/FAQQuestion/FAQQuestion';
import history from '../../../../utils/history';

const _renderFrequentlyAskedQuestions = () =>
  frequentlyAskedQuestions.map((item, index) => (
    <FAQQuestion
      key={item.question}
      questionNumber={index + 1}
      question={t(item.question)}
      answer={t(item.answer)}
    />
  ));

const QuestionsSection = () => (
  <div className="block-faq">
    <img src={faqIcon} alt="" />
    <div className="container">
      <h2> {t(`${HOME_QUESTIONS_SECTION_NAMESPACE}haveQuestions`)}</h2>
      <h3>{t(`${HOME_QUESTIONS_SECTION_NAMESPACE}checkoutOurPlatform`)}</h3>
      <Formik
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          if (values.searchValue) {
            history.push('/faq', {
              searchValues: values.searchValue
                .trim()
                .toLowerCase()
                .split(' '),
            });
          }
          setSubmitting(false);
        }}
      >
        {({ handleSubmit, values, handleChange, handleBlur }) => (
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <input
                name="searchValue"
                className="form-control"
                type="text"
                autoComplete="off"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.searchValue}
              />
            </div>
            <div className="form-group">
              <button className="btn btn-primary btn-sm" type="submit" />
            </div>
          </form>
        )}
      </Formik>
      <div className="list-faq">
        <h3>
          {`${t(
            `${HOME_QUESTIONS_SECTION_NAMESPACE}frequentlyAskedQuestions`,
          )}`}
        </h3>
        <div className="list-accordion">
          {_renderFrequentlyAskedQuestions()}
        </div>
      </div>
      <div className="contact-list">
        <Link to="/">
          <span className="img-appel" />
          <span className="text">
            {`${t(`${HOME_QUESTIONS_SECTION_NAMESPACE}contactUs`)}`}
          </span>
        </Link>
        <Link to="/">
          <span className="img-envoi" />
          <span className="text">
            {`${t(`${HOME_QUESTIONS_SECTION_NAMESPACE}sendMail`)}`}
          </span>
        </Link>
        <Link to="/map">
          <span className="img-trouve" />
          <span className="text">
            {`${t(`${HOME_QUESTIONS_SECTION_NAMESPACE}findAgency`)}`}
          </span>
        </Link>
        <Link to="/faq">
          <span className="img-faq" />
          <span className="text">
            {`${t(`${HOME_QUESTIONS_SECTION_NAMESPACE}faq`)} `}
          </span>
        </Link>
      </div>
    </div>
  </div>
);
export default withTranslation()(QuestionsSection);
